


class Gps {
	constructor(arg) {
		this.lock = false //锁防止重复请求
	}
	async getLocation(param = {
		type: 'wgs84'
	}) {
		return new Promise(async (callback) => {
			if (this.lock) {
				// console.log('已锁，已有另一个请求正在执行。无需重复请求');
				callback(false);
				return false
			}
			this.lock = true //加锁防止重复的请求
			uni.getLocation({
				...param,
				success: res => {
					this.lock = false //成功后解锁
					//console.log(res);
					callback(res)
				},
				fail: async (err) => {
					uni.showToast({
						title: '定位获取失败',
						icon: 'none'
					});
					console.error(err)
					callback(false)
					



					
					

					if (err.errMsg == 'getLocation:fail auth deny') {
						uni.showModal({
							content: '应用无定位权限',
							confirmText: '前往设置',
							complete: (e) => {
								if (e.confirm) {
									uni.openSetting({
										success(res) {
											console.log(res.authSetting)
										}
									});
								}
								this.lock = false //解锁让回到界面重新获取
							}
						});
					}
					if (err.errMsg == 'getLocation:fail:ERROR_NOCELL&WIFI_LOCATIONSWITCHOFF') {
						uni.showModal({
							content: '未开启定位权限，请前往手机系统设置中开启',
							showCancel: false,
							confirmText:"知道了"
						});
					}

				}
			});
		})
	}

































































}
export default Gps
